
html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}

#root {
  height: 100%;
}

.dropdown-container {
  margin: auto;
  height: 100%;
  display: flex;
  align-items: center;
  width: 100%;
}

.result-header {
  width: fit-content;
  margin: auto;
  height: fit-content;
  display: flex;
  align-items: first baseline;
  justify-content: center;
  transform: translate(-160%, 0%);
}
.dropdown-table {
  padding-top: 4rem;
  border-collapse: collapse;
  width: 100%;
}

.dropdown-table th,
.dropdown-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
  white-space: normal;
}

.dropdown-select {
  padding: 0.7rem 1.2rem;
  font-size: 1rem;
  border: 1px solid var(--primary-color);
  border-radius: 5px;
  outline: none;
  background-color: #fff;
  color: var(--primary-color);
  transition: all 0.2s ease-in-out;
}

.dropdown-select:hover {
  cursor: pointer;
  background-color: var(--primary-color-alpha);
}

.dropdown-select:focus {
  box-shadow: 0px 0px 0px 1px var(--primary-color);
}
.dropdown-container-button {
  margin-top: 2rem;
  display: flex;
  justify-content: flex-start;
}

.dropdown-container-button button {
  border-radius: 4px;
  background: #9cb3db;
  border: 1px solid #fff;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 500;
  padding: 0.7rem 1.2rem;
  outline: none;
  cursor: pointer;
  transition: 0.3s ease;
}

.dropdown-container-button button:hover {
  background: #9cb3db;
}

.checkbox-container {
  display: flex;
  flex-direction: row;
  margin-left: -5px;
}

.checkbox-label {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-right: 10px;
}

.checkbox-custom {
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 2px solid #ccc;
  margin-right: 5px;
}

input[type="checkbox"] {
  display: none;
}

input[type="checkbox"]:checked + .checkbox-custom {
  background-color: #3f51b5;
  border-color: #3f51b5;
}

option {
  color: #000;
}

.dropdown-container2 {
  width: 500px;
  margin: auto;
  height: 100%;
  display: flex;
  align-items: center;
}

.containter {
  display: flex;
  flex-direction: column;
  position: relative;

  margin: 1rem 0rem;
}

.containter label {
  margin-bottom: 0.45rem;
  color: #9cb3db;
  font-size: 12px;
  font-family: Arial, Helvetica, sans-serif;
  letter-spacing: 0.7px;
  line-height: 1.3;
}

.containter input {
  padding: 0.55rem 0.4rem;
  border: 1px solid var(--primary-color);
  border-radius: 4px;
  font-size: 14px;
  outline: none;
  transition: all 150ms ease-in-out 0s;
  width: 85%;
  background: #fff;
}

.containter input::placeholder {
  color: var(--primary-color);
  width: 100%;
  font-weight: unset;
  font-family: Arial, Helvetica, sans-serif;
}

.containter input:hover {
  background-color: var(--primary-color-alpha);
}

.containter input:focus,
.containter input:active {
  box-shadow: 0px 0px 0px 1px var(--primary-color);
}

.containter-password {
  position: absolute;
  right: 13%;
  top: 50%;
  cursor: pointer;
}

.containter2 {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 1233px;

  margin: 1rem 0rem;
}
